.dialog_modal {
  position: fixed;
  top: 80px;
  right: 72px;
  z-index: 1000;
  overflow: auto;
}

.dialog_modal_body {
  box-sizing: border-box;
  padding: 32px 16px;
  width: 325px;
  height: 677px;
  background: #fff;
}

.dialog_bag,
.dialog_bag span {
  margin-bottom: 32px;
  font-size: 16px;
  line-height: 26px;
}

.dialog_bag {
  font-weight: 700;
}

.dialog_bag span {
  font-weight: 500;
}

.dialog_price_wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 32px;
}

.dialog_total {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: #1d1f22;
}

.dialog_price {
  font-weight: 700;
  font-size: 16px;
  line-height: 26px;
}

.dialog_btn_wrapper {
  display: flex;
  gap: 12px;
  justify-content: space-between;
  margin-top: 32px;
}

.dialog_btn_cart,
.dialog_btn_order {
  padding: 12px 30px;
  border: 1px solid #1d1f22;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  text-transform: uppercase;
  transition: 0.3s;
}

.dialog_btn_order:hover {
  background-color: #389952;
}

.dialog_btn_order {
  background: #5ece7b;
  border: none;
  color: #fff;
}

.dialog_modal_background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(57, 55, 72, 0.22);
  z-index: 900;
}

.header_cart {
  position: relative;
}

.header_cart_badge {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  text-align: center;
  background: #1d1f22;
  color: #fff;
  z-index: 0;
}
